import React from "react"
import { useState, useEffect } from 'react';

import * as  axios from 'axios';

const WakaTime = () => {
    const [time, setTime] = useState(null);
    const apiKey = "0c75d00e-e2f6-44d1-b6ea-1183eaa39116";
    const url = "https://wakatime.com/share/@1676c4bf-6c0d-4787-bb18-032d242635f5/6fb1ff53-56a5-4e3c-a088-84bc81db1b86.json";

    const headers = {
            'Authorization': 'Basic ' + apiKey,
    }


    useEffect(() => {
        async function getTime() {
          if(typeof window !== null) {
            const time = await axios.get(url);

            let totalSeconds = 0;
            for (let i = 0; i < time.data.data.length; i++) {
              totalSeconds += time.data.data[i].grand_total.total_seconds;
            }
            let totalHours = totalSeconds / 3600 ;
            let totalMinutes = totalSeconds /60;

            let hours = Math.floor(totalHours);
            let minutes = (Math.floor((totalSeconds /60) % 60 ));
            console.log(minutes);
            
            setTime(hours +"hrs " + minutes + "mins");
          }
        }
        getTime();
     }, [])

  return (
    <div className="wakatime-weekly">
      {time != null && <p><span className="time-text">{time}</span> spent coding this week</p>}
    </div>
  )
}

export default WakaTime
