import React from "react"

import Carousel from "../carousel"

import typetest1 from "../../images/carousel/typetest/typetest1.jpg"
import typetest2 from "../../images/carousel/typetest/typetest2.jpg"
import typetest3 from "../../images/carousel/typetest/typetest3.jpg"

function TypeTestText(props) {

  return (
    <div>
      <Carousel
        images={[
          { image: typetest1, alt: "Website homepage screenshot" },
          { image: typetest2, alt: "Website tour page screenshot" },
          { image: typetest3, alt: "Website music page screenshot" }
        ]}
      />
        This website started as part of a research project, looking at how to improve users typing speed when copying down something from a display. As the user played the text’s typeface, colour, and letter spacing all changed. From this I was able to determine the best combination of these styles, and used this to create typetest.  
       <br /><strong>It was created using React
</strong>
    </div>
  )
}

export default TypeTestText